import { Grid } from '@mui/material'
import React from 'react'
import './Team.css'

import founder from '../../assets/founder.jpg'
import cofounder1 from '../../assets/co-founder.jpg'
import cofounder2 from '../../assets/co-founder2.jpg'
import bg from '../../assets/bg.jpg'

import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import TwitterIcon from '@mui/icons-material/Twitter';

const team = () => {
  return (
    <div className="team-main">

      <div className="team-aboutus">
        <span className='team-title'>Who we are?</span>
        <span className='team-about'>here is our team</span>
      </div>
      <Grid container className='team-container' rowSpacing={5} columnSpacing={{ md: 1, sm: 3 }}>
        <Grid className='Grid-container-1' item md={4}>
          <div className="team-card">
            <div className="card-image">
              <img src={bg} alt="" />
            </div>
            <div className="profile-image">
              <img src={cofounder1} alt="" />
            </div>
            <div className="card-content">
              <span className='person-name'>Vikash Sharma </span>
              <span className='person-position'>Co-Founder</span>
              <span className='person-detail'>
                Vikas, our exceptional co-founder, brings together technical brilliance, business acumen, and exceptional management skills. With expertise in AI/ML, Data Science, and Blockchain, he plays a vital role in our company's strategic growth and innovation in the evolving technology landscape.</span>
            </div>
            <div className="icons">
              <a href="https://instagram.com/thisisvikas_" target='_blank'>
                <InstagramIcon style={{ fontSize: 32 }} className='Sicon'></InstagramIcon>
              </a>
              <a href="https://www.linkedin.com/in/vikas-sharma005" target='_blank'>
                <LinkedInIcon style={{ fontSize: 32 }} className='Sicon'></LinkedInIcon>
              </a>
              <a href="https://twitter.com/hitechvikas05" target='_blank'>

                <TwitterIcon style={{ fontSize: 32 }} className='Sicon'></TwitterIcon>
              </a>
            </div>
          </div>
        </Grid>
        <Grid className='Grid-container-2' item md={4}>
          <div className="team-card">
            <div className="card-image">
              <img src={bg} alt="" />
            </div>
            <div className="profile-image">
              <img src={founder} alt="" />
            </div>
            <div className="card-content">
              <span className='person-name'>Shaishav Surati</span>
              <span className='person-position'>Founder</span>
              <span className='person-detail'>
                Passionate Bachelor's student with expertise in Business Development, AI-ML Research, and Blockchain. Driven by innovation and strategic thinking, eager to shape the future of technology and business. Let's make an impact together.</span>
            </div>
            <div className=" icons">
              <a href="https://instagram.com/withssp.dev" target='_blank'>
                <InstagramIcon style={{ fontSize: 32 }} className='Sicon'></InstagramIcon>
              </a>
              <a href="https://www.linkedin.com/in/shaishavsurati0699" target='_blank'>
                <LinkedInIcon style={{ fontSize: 32 }} className='Sicon'></LinkedInIcon>
              </a>
              <a href="https://twitter.com/shaishavsurati" target='_blank'>

                <TwitterIcon style={{ fontSize: 32 }} className='Sicon'></TwitterIcon>
              </a>
            </div>
          </div>
        </Grid>
        <Grid className='Grid-container-3' item md={4}>
          <div className="team-card">
            <div className="card-image">
              <img src={bg} alt="" />
            </div>
            <div className="profile-image">
              <img src={cofounder2} alt="" />
            </div>
            <div className="card-content">
              <span className='person-name'>Nisarg Dholaria</span>
              <span className='person-position'>Co-Founder</span>
              <span className='person-detail'>
                Skilled Data Science enthusiast from the University of Arizona, experienced in machine learning, statistics, and data visualization. Delivering impactful solutions through data-driven decision-making and effective communication. Let's unlock your data's potential and shape an innovative future together.</span>
            </div>
            <div className="icons">
              <a href="https://www.linkedin.com/in/nisarg-dholariya-74923a12b" target='_blank'>
                <LinkedInIcon style={{ fontSize: 32 }} className='Sicon'></LinkedInIcon>
              </a>
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  )
}

export default team